@mixin w {
  @media only screen and (max-width: "1599px") {
    @content; } }
@mixin x {
  @media only screen and (max-width: "1439px") {
    @content; } }
@mixin mac {
  @media only screen and (max-width: "1365px") {
    @content; } }
@mixin d {
  @media only screen and (max-width: "1279px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin a {
  @media only screen and (max-width: "639px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');
@mixin f {
  font-family: 'Poppins', sans-serif; }

$dark: #111029;
$bl: #00113B;
$wh: #fff;
$purple: #2B6BFF;
$red: #FF6800;
$yellow: #FFD301;
$pink: #111029;
$gray-color: #9F9FA9;
$gray-text: #6b6b6b;

html {
  // height: 100%
  &.no-scroll {
    overflow: hidden; } }

body {
  min-width: 375px;
  @include f;
  font-size: 18px;
  line-height: (32/18);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $dark;
  @include d {
    font-size: 16px;
    line-height: (28/16); }
  &.no-scroll {
    overflow: hidden; } }

button,
input,
textarea,
select {
  @include f; }

.page {
  position: relative;
  overflow: hidden;
  min-height: 100vh; }

.center {
  max-width: 1334px;
  margin: 0 auto;
  padding: 0 82px;
  @include t {
    padding: 0 48px; }
  @include m {
    padding: 0 32px; } }

.btn {
  min-width: 240px;
  height: 72px;
  border-radius: 20px;
  @include f;
  font-size: 18px;
  font-weight: 500;
  transition: all .25s;
  @include d {
    min-width: 200px;
    height: 64px;
    border-radius: 12px;
    font-size: 16px; }
  .icon,
  &__text {
    display: inline-block;
    vertical-align: middle; }
  .icon {
    @include nf {
      margin-left: 16px; }
    @include nl {
      margin-right: 16px; } }
  .icon {
    font-size: 16px;
    @include d {
      font-size: 14px; } }
  &:hover {
    transform: translateY(-2px); }
  &:active {
    transform: translateY(2px); }
  &_purple {
    background: $purple;
    color: $wh; }
  &_dark {
    background: $bl;
    color: $wh; } }

a.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center; }

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-weight: 600; }

.h1 {
  font-size: 72px;
  line-height: (84/72);
  letter-spacing: -.5px;
  @include d {
    font-size: 56px;
    line-height: (72/56);
    letter-spacing: -.4px; }
  @include m {
    font-size: 42px;
    line-height: (56/42);
    letter-spacing: -.1px; } }

.h2 {
  font-size: 56px;
  line-height: (72/56);
  letter-spacing: -.4px; }

.h3 {
  font-size: 42px;
  line-height: (56/42);
  letter-spacing: -.1px;
  @include d {
    font-size: 32px;
    line-height: (48/32);
    letter-spacing: -.15px; }
  @include m {
    font-size: 20px;
    line-height: (28/20); } }

.h4 {
  font-size: 32px;
  line-height: (48/32);
  letter-spacing: -.15px;
  @include d {
    font-size: 24px;
    line-height: (32/24); } }

.h5 {
  font-size: 24px;
  line-height: (32/24);
  @include d {
    font-size: 20px;
    line-height: (28/20); } }

.h6 {
  font-size: 20px;
  line-height: (28/20); }

.stage {
  position: relative;
  margin-bottom: 24px;
  padding-left: 68px;
  font-size: 20px;
  line-height: 1.4;
  font-weight: 600;
  color: $purple;
  @include d {
    margin-bottom: 16px;
    font-size: 16px; }
  &:before {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;
    width: 52px;
    height: 2px;
    background: $purple;
    @include d {
      top: 10px; } }
  &_pink {
    color: $pink;
    &:before {
      background: $pink; } } }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding: 48px 0;
  @include d {
    padding: 32px 0; }
  @include m {
    padding: 36px 0; }
  &__center {
    display: flex; }
  & > &__center {
    align-items: center;
    max-width: 100%;
    padding: 0 68px;
    @include d {
      padding: 0 48px; }
    @include m {
      padding: 0 32px; } }
  &__center &__logo {
    margin-right: 93px;
    @include d {
      margin-right: 68px; }
    @include t {
      margin-right: 40px; }
    @include m {
      position: relative;
      z-index: 15;
      margin-right: auto; } }
  &__logo {
    font-size: 0; }
  &__logo &__pic {
    width: 90px;
    @include d {
      width: 40px; }
    @include m {
      width: 70px; } }
  &__social {
    display: flex;
    align-items: center;
    &_main {
      margin-right: auto;
      @include m {
        display: none; } }
    &_inner {
      position: absolute;
      left: calc(50% - 184px);
      bottom: 82px;
      @include d {
        left: 380px;
        bottom: 56px; }
      @include m {
        position: relative;
        left: auto;
        bottom: auto;
        margin-top: 20px; }
      @include a {
        flex-direction: column;
        align-items: flex-start;
        margin-top: 32px; } } }
  &__social &__link {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    color: $gray-color;
    transition: color .25s;
    @include d {
      font-size: 14px; }
    .icon {
      margin-right: 15px;
      font-size: 16px;
      fill: $gray-color;
      transition: fill .25s; }
    &:hover {
      color: $dark;
      .icon {
        fill: $dark; } }
    @include nl {
      margin-right: 68px;
      @include t {
        margin-right: 40px; }
      @include a {
        margin: 0 0 16px; } } }
  &__burger {
    position: relative;
    z-index: 12;
    width: 64px;
    height: 64px;
    margin-right: -17px;
    border-radius: 50%;
    padding: 5px;
    background: transparent;
    font-size: 0;
    transition: all .25s;
    @include d {
      width: 48px;
      height: 48px;
      margin-right: -10px; }
    @include m {
      width: 32px;
      height: 32px;
      margin-right: -5px;
      padding: 0; }
    &:before,
    &:after {
      content: "";
      display: inline-block;
      width: 32px;
      height: 2px;
      background: $wh;
      transition: transform .25s;
      @include d {
        width: 24px; }
      @include m {
        background: $dark; } }
    &:before {
      margin-bottom: 4px;
      @include d {
        margin-bottom: 2.5px; } }
    &:after {
      margin-top: 4px;
      @include d {
        margin-top: 2.5px; } }
    &_dark {
      &:before,
      &:after {
        background: $bl; } }
    &:hover {
      opacity: .85; }
    &.active {
      background: $purple;
      @include m {
        background: none; }
      &:before,
      &:after {
        background: $wh;
        @include m {
          background: $bl; } }
      &:before {
        transform: translateY(5px) rotate(45deg);
        @include d {
          transform: translateY(3.5px) rotate(45deg); } }
      &:after {
        transform: translateY(-5px) rotate(-45deg);
        @include d {
          transform: translateY(-3.5px) rotate(-45deg); } } } }
  &__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    z-index: 10;
    background: $wh;
    visibility: hidden;
    opacity: 0;
    transition: all .25s;
    @include m {
      display: block; }
    @include a {
      visibility: visible;
      opacity: 1;
      transform: translateX(100%);
      background: $wh url('../img/bg-header-mobile.png') no-repeat 100% 150% / 100% auto; }
    &.visible {
      visibility: visible;
      opacity: 1;
      @include a {
        transform: translateX(0); } } }
  &__preview {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 400px;
    padding: 64px 0 0 68px;
    background: #1B1B87;
    overflow: hidden;
    @include d {
      width: 300px;
      padding: 46px 0 0 48px; }
    @include m {
      display: none; } }
  &__img {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    height: 100%;
    max-height: 100%;
    font-size: 0;
    @include t {
      height: 80%;
      max-height: 80%; } }
  &__img &__pic {
    height: 100%;
    width: auto; }
  &__wrapper &__center {
    align-items: stretch;
    width: 100%;
    max-width: 1204px;
    padding-left: 418px;
    @include d {
      max-width: 100%;
      padding-left: 380px; }
    @include t {
      flex-direction: column-reverse; }
    @include m {
      display: block;
      padding: 120px 32px 30px; } }
  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-shrink: 0;
    width: 340px;
    margin-right: auto;
    @include d {
      width: 280px; }
    @include t {
      margin: 120px 0 0; }
    @include m {
      display: none; } }
  &__line {
    display: flex;
    color: $dark;
    @include d {
      font-size: 16px;
      line-height: (28/16); }
    @include nl {
      margin-bottom: 24px; } }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 24px;
    height: 30px;
    margin-right: 45px;
    font-size: 0;
    @include d {
      margin-right: 25px;
      width: 17px;
      height: 28px; }
    .icon {
      fill: $purple;
      &-email {
        font-size: 20px;
        @include d {
          font-size: 14px; } }
      &-place {
        font-size: 23px;
        @include d {
          font-size: 17px; } }
      &-phone {
        font-size: 24px;
        @include d {
          font-size: 18px; } } } }
  &__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: -15px; }
  &__item {
    overflow: hidden;
    font-size: 72px;
    line-height: (84/72);
    letter-spacing: -.5px;
    font-weight: 600;
    color: $dark;
    transition: color .25s;
    @include d {
      font-size: 42px;
      line-height: (56/42); }
    @include m {
      font-size: 24px;
      line-height: 1.5; }
    @include a {
      font-size: 32px; }
    span {
      display: block;
      transform: translateY(110%);
      transition: transform .25s;
      transition-delay: .3s; }
    &:hover {
      color: $purple; }
    @include nl {
      margin-bottom: 16px;
      @include d {
        margin-bottom: 8px; }
      @include m {
        margin-bottom: 0; } } }
  &__wrapper.visible &__item {
    span {
      transform: translateX(0); }
    &:first-child span {
      transition-delay: .2s; }
    &:nth-child(2) span {
      transition-delay: .4s; }
    &:nth-child(3) span {
      transition-delay: .6s; }
    &:nth-child(4) span {
      transition-delay: .8s; } } }

.main {
  position: relative;
  padding: 270px 0 105px;
  @include d {
    padding: 170px 0 60px; }
  @include m {
    padding: 130px 0 0; }
  @include a {
    &:before {
      content: "";
      position: absolute;
      top: 420px;
      right: 0;
      z-index: -1;
      width: 226px;
      height: 370px;
      background: #1B1B87;
      clip-path: polygon(0 120px, 100% 0, 100% 100%, 0 100%); } }
  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -5;
    @include x {
      right: -50px; }
    @include mac {
      right: -100px; }
    @include d {
      right: 0; }
    @include t {
      right: -150px; }
    @include m {
      display: none; } }
  &__bg &__pic {
    max-width: 1440px;
    @include d {
      max-height: 852px; } }
  &__wrap {
    max-width: 490px;
    @include d {
      max-width: 380px; }
    @include m {
      max-width: 100%; }
    @include a {
      max-width: 230px; } }
  &__title {
    perspective: 400px;
    // perspective-origin: 50% 50%
    transform-style: preserve-3d;
    margin-bottom: 181px;
    @include d {
      margin-bottom: 114px; }
    @include m {
      margin-bottom: 40px;
      font-size: 32px;
      line-height: (48/32);
      letter-spacing: -.15px; }
    span {
      display: block; } }
  &__view {
    position: absolute;
    top: 320px;
    right: 0;
    width: 910px;
    z-index: -1;
    @include x {
      right: -50px; }
    @include mac {
      right: -100px; }
    @include d {
      top: 218px;
      right: -8px;
      width: 660px; }
    @include t {
      top: 327px;
      right: -56px;
      width: 500px; }
    @include m {
      position: relative;
      top: auto;
      left: auto;
      right: auto;
      width: 480px;
      margin: 40px 0 0 12%; }
    @include a {
      margin: 130px 0 0 -63px; }
    &[data-aos] {
      .main__preview {
        transform: translateY(40px);
        opacity: 0;
        transition: all .3s; }
      &.aos-animate {
        .main__preview {
          transform: translateY(0);
          opacity: 1;
          &:first-child {
            transition-delay: .9s; }
          &:nth-child(2) {
            transition-delay: .15s; }
          &:nth-child(3) {
            transition-delay: .3s; }
          &:nth-child(4) {
            transition-delay: .45s; }
          &:nth-child(5) {
            transition-delay: .6s; }
          &:nth-child(6) {
            transition-delay: .75s; }
          &:nth-child(7) {
            transition-delay: 1.05s; } } } } }
  &__view &__pic,
  &__figure &__pic {
    width: 100%; }
  &__preview {
    &:nth-child(2) {
      left: 43.7%;
      top: -16%;
      z-index: 3;
      width: 17%; }
    &:nth-child(3) {
      left: 35.6%;
      top: -14%;
      z-index: 2;
      width: 33.5%; }
    &:nth-child(4) {
      left: 19.1%;
      top: 19.9%;
      z-index: 1;
      width: 33.5%; }
    &:nth-child(5) {
      right: 14.2%;
      top: 20%;
      z-index: 1;
      width: 25%; }
    &:nth-child(6) {
      right: 43.3%;
      top: 35.5%;
      z-index: 3;
      width: 8%; }
    &:nth-child(7) {
      right: 2%;
      bottom: 8.4%;
      z-index: 3;
      width: 27.3%; }
    @include nf {
      position: absolute; } }
  &__figure {
    position: absolute;
    z-index: -3;
    &:first-child {
      top: 152px;
      right: 590px;
      width: 231px;
      @include mac {
        right: 520px; }
      @include d {
        top: 240px;
        right: 466px;
        width: 132px; }
      @include t {
        top: 140px;
        right: 320px; }
      @include m {
        top: 110px;
        right: 10%; } }
    &:nth-child(2) {
      right: 738px;
      bottom: 21.2%;
      width: 213px;
      @include mac {
        right: 700px; }
      @include d {
        right: 541px;
        bottom: 16.3%;
        width: 180px; }
      @include t {
        right: 400px; }
      @include m {
        top: 260px;
        bottom: auto;
        right: -30px; } }
    &:nth-child(3) {
      right: 627px;
      bottom: 6.2%;
      width: 250px;
      @include mac {
        right: 530px; }
      @include d {
        right: 467px;
        bottom: 4.3%;
        width: 176px; }
      @include t {
        right: 300px;
        bottom: 3%; }
      @include m {
        display: none; } } }
  &__bg,
  &__preview,
  &__figure {
    font-size: 0;
    pointer-events: none; }
  .scroll {
    @include m {
      font-size: 0; } } }

.scroll {
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 500;
  color: $wh;
  @include d {
    font-size: 14px; }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 80px;
    height: 80px;
    margin-right: 24px;
    border-radius: 50%;
    background: $wh;
    box-shadow: 0 5px 20px rgba($dark, .05);
    font-size: 0;
    transition: transform .25s;
    @include d {
      width: 56px;
      height: 56px;
      margin-right: 16px; }
    @include m {
      box-shadow: 0 10px 25px rgba($dark, .1); } }
  &__pic {
    @include d {
      width: 16px; } }
  &:hover &__icon {
    transform: translateY(3px); } }

.details {
